import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams, ModalController } from 'ionic-angular';
import { MaincategoryPage } from './maincategory/maincategory';
import { SubcategoryPage } from './subcategory/subcategory';


@IonicPage()
@Component({
  selector: 'page-category',
  templateUrl: 'category.html',
})
export class CategoryPage {

  main_category:string = "Select Main Category";
  sub_category:string ="Select Sub Category";
  is_main_selected:boolean =false;
  is_category_image:boolean =false;
  is_error:boolean =false;
  error_text:string ="This is sample error";
  image = "../assets/imgs/397.jpg";
  constructor(public navCtrl: NavController, public navParams: NavParams,public modalCtrl: ModalController) {
    if(MaincategoryPage.is_main_selected){
      var data =  this.navParams.get("item");
      this.main_category = data.toString();
      console.log(data);
    }
  }

  ionViewDidLoad() {
  
  }
 
  ioViewDidReload(){
    console.log('ionViewDidResume CategoryPage');
  }

  getMainCategory(){
      this.navCtrl.push(MaincategoryPage);
  }

  getSubCategory(){
  //  this.navCtrl.push(SubcategoryPage);
    console.log('ionViewDidLoad CategoryPage');

 
  }

}



